body {
  background-color: var(--background-color-light);
  font-family: var(--main-text-font);
  color: var(--main-text-color);
}

.img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li, ul {
  list-style: none;
}

button {
  cursor: pointer;
  margin: 0;
  padding: 0;
  display: block;
}

html {
  scroll-behavior: smooth;
}

.container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0 16px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

:root {
  --main-text-color: #111321;
  --light-text-color: #fff;
  --gray-text-color: #f4f4f4;
  --blue-text-color: #4440f6;
  --light-switch-color: #111321;
  --dark-switch-color: #4b48db;
  --background-color-dark: #2e2e2e;
  --background-color-light: #fff;
  --background-color-footer: #a8a8a8;
  --background-color-accent: #4440f6;
  --border-color: #4b48db;
  --line-color-dark: #f4f4f4;
  --line-color-dark: #000;
  --main-text-font: Manrope, sans-serif;
  --weather-text-font: Roboto, sans-serif;
  --second-text-font: Poppins, sans-serif;
  --transition-duration: 300ms;
  --transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

